export default function VideosPage() {
  const videos = [
    {
      id: 1,
      title: 'React Hooks 超入門 - useEffect を最速で理解する',
      thumbnailUrl: 'https://i.ytimg.com/vi/Kot5hM5eHWQ/hqdefault.jpg',
      youtubeAuthor: 'Tech Channel',
      postedBy: {
        name: 'もと',
        iconUrl: 'https://i.pravatar.cc/100?u=user1',
      },
      memo: '10:20〜 の説明がめちゃわかりやすい。useEffect の依存配列の理解に最適。',
      createdAt: '2025-02-10T10:00:00Z',
    },
    {
      id: 2,
      title: 'Next.js App Router 解説 - 基礎から理解する',
      thumbnailUrl: 'https://i.ytimg.com/vi/MXSN6wZTo4M/hqdefault.jpg',
      youtubeAuthor: 'Next Tutorials',
      postedBy: {
        name: 'ゆうき',
        iconUrl: 'https://i.pravatar.cc/100?u=user2',
      },
      memo: 'ページ遷移の仕組みの説明が神。14:00〜を必ず見る。',
      createdAt: '2025-02-09T08:30:00Z',
    },
  ];

  return (
    <div className="max-w-6xl mx-auto p-6">
      <h1 className="text-3xl font-bold text-center mb-10">みんなの学習メモ</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {videos.map((v) => (
          <a
            key={v.id}
            href={`/videos/${v.id}`}
            className="group rounded-xl overflow-hidden border border-gray-200 bg-white shadow-sm hover:shadow-xl transition-all duration-300"
          >
            {/* サムネイル（16:9） */}
            <div className="w-full aspect-video overflow-hidden bg-gray-100">
              <img
                src={v.thumbnailUrl}
                alt={v.title}
                className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
              />
            </div>

            {/* カード内容 */}
            <div className="p-4 space-y-3">
              {/* タイトル */}
              <h2 className="text-lg font-semibold line-clamp-2 group-hover:text-blue-600 transition-colors">
                {v.title}
              </h2>

              {/* YouTube 投稿者 */}
              <p className="text-sm text-gray-600">{v.youtubeAuthor}</p>

              {/* 投稿したアプリユーザー */}
              <div className="flex items-center gap-3 mt-2">
                <img
                  src={v.postedBy.iconUrl}
                  alt={v.postedBy.name}
                  className="w-9 h-9 rounded-full object-cover"
                />
                <span className="text-sm text-gray-800">{v.postedBy.name}</span>
              </div>

              {/* メモ（1〜2行） */}
              <p className="text-sm text-gray-700 line-clamp-2">{v.memo}</p>

              {/* 投稿日時 */}
              <p className="text-xs text-gray-400">
                {new Date(v.createdAt).toLocaleDateString('ja-JP')}
              </p>
            </div>
          </a>
        ))}
      </div>
    </div>
  );
}
